
//  viewport  ! (, IE10)
// http://timkadlec.com/2012/10/ie10-snap-mode-and-responsive-design/
@at-root {

  @-ms-viewport {
    width: device-width;
  }
}

.page { // html

  $block-name:                &; // #{$block-name}__element

  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-overflow-style: scrollbar;
  -ms-text-size-adjust: 100%;
  min-width: 320px;
  min-height: 100%;

  &__inner {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }

  &__content {
    flex-grow: 1;
  }

  &__main-content {
    @include container;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  &__main-banner {
    margin-bottom: 20px;
  }

  &__footer-wrapper {
    flex-shrink: 0;
  }

  &__part {

  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  //      typo
  display: flex;           //    IE  min-height and flexbox (flex-direction:column)
  flex-direction: column;  //   footer  IE 10-11
  background-color: #fff;
  margin: 0;
  min-height: 100%;
  background: #f9f9f9;
}

[tabindex='-1']:focus {
  outline: none !important;
}

//         iOS
[role='button'] {
  cursor: pointer;
}

//    (  , ).
input[type='search'] {
  -webkit-appearance: textfield;
}

// area,
// summary,
a,
button,
[role='button'],
input,
label,
select,
textarea {
  touch-action: manipulation; //   300ms  (  )
  margin: 0;                  //  margin  Firefox  Safari
}

button,
input {
  overflow: visible;    //    Edge
  text-transform: none; //   text transform  Firefox
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; //     Safari
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox; //   Mobile Safari (https://github.com/twbs/bootstrap/issues/11266)
}

textarea {
  overflow: auto;   //  .   IE.
  resize: vertical; //   
}

svg {

  a &,
  button & {
    pointer-events: none; //   Edge,  ! ()
  }

  &:not(:root) {
    overflow: hidden; //      IE
  }
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: $link-color;
  background-color: transparent;
  text-decoration-skip: ink;
  -webkit-text-decoration-skip: objects;
  transition: color 0.3s;

  &:hover,
  &:focus {
    color: $link-color--hover;
  }
}

hr {
  box-sizing: content-box; //  Firefox
  overflow: visible;       //  Edge  IE
}

.gray {
  background: #f3f2f2;
}
