// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.sidebar-section {

  $block-name:                &; // #{$block-name}__element

  @include row;
  margin-bottom: 30px;

  &__main-col {
    @include col();
    @include col(lg, 9);
  }

  &__sidebar-col {
    @include col();
    @include col(lg, 3);
  }
}
