//        - comment,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

$screen-md:                   768px !default;

.comment {

  $block-name:                &; // #{$block-name}__element

  margin-top: 1rem;
  margin-bottom: 1rem;

  @media (min-width: $screen-md) {

    & & {
      padding-left: calc(60px + 1rem);
    }

    & & & & {
      padding-left: 0;
    }
  }

  &__inner {
    display: flex;
  }

  &__avatar-wrap {
    flex-shrink: 0;
    margin-right: 1rem;

    img {
      display: block;
      width: 60px;
      height: 60px;
      object-fit: cover;
    }
  }

  &__text {
    flex-grow: 1;

    *:first-child {
      margin-top: 0;
    }
  }

  &__header {}

  &__footer {}
}
