.lk-sidebar {

  $block-name:                &; // #{$block-name}__element

  border: 1px solid #a2a2a2;
  border-radius: 6px;
  background: #fff;
  padding: 30px;
  min-height: 345px;
  margin-bottom: 20px;

  @media (max-width: ($screen-lg - 1)) {
    min-height: auto;
  }
}
