//        - latest-news,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.latest-news {

  $block-name:                &; // #{$block-name}__element

  &__item {
    margin-bottom: rem(25px);
  }

  &__title {
    font-weight: 400;
    margin-bottom: 5px;

    a {
      color: #000;
      text-decoration: none;
    }
  }

  &__date {
    color: #7d7a7a;
    font-size: 12px;

    span {

      &:not(:last-child) {

        &:after {
          content: "|";
          display: inline-block;
          margin: 0 4px;
        }
      }
    }
  }
}
