//        - main-shops-list,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.main-shops-list {

  $block-name:                &; // #{$block-name}__element

  @include row;
  padding: 0 20px 0 20px;

  &__card {
    @include col();
    @include col(lg, 3);
    @include col(md, 4);
    @include col(sm, 6);
    margin-bottom: rem(20px);

    h3 {
      margin-top: 0;
    }

    p {
      margin: 0;
    }
  }
}
