//        - pie-chart,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

$text-color:                  hsl(0, 0%, 10%) !default;
$font-size:                   1rem !default;

$gray-light:                  hsl(0, 0%, 60%) !default;
$gray-lighter:                hsl(0, 0%, 80%) !default;

.pie-chart {

  $block-name:                &; // #{$block-name}__element

  .js & {

    display: inline-block;
    position: relative;
    min-width: 100px;  //     (  pie-chart.js)
    min-height: 100px; //     (  pie-chart.js)

    &__svg {
      display: block;
      transform: rotate(-90deg);
    }

    &__circle-back {
      fill: $gray-lighter;
    }

    &__circle {
      fill: $gray-lighter;
      stroke: $gray-light;
    }

    &__descr {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
